.crf-wrap{
    .ant-table{
        th{
            white-space: nowrap;
        }
    }
    .ant-pagination {
        text-align: right;
        padding:15px 0;
    }
    .search-bar{
        max-width:400px;
        .warn-tip{
            height:30px;
            line-height:30px;
            color:red;
        }
    }
    .opt{
        cursor: pointer;
    }
    .search-wrap{
        display: flex;
        // border:1px solid #d9d9d9;
        input{
            border-radius: 0;
            border-right:0;
            // border:0;
            // &:focus{
            //     border:none;
            //     border-color:none;
            //     box-shadow:none;
            // }
        }
        button{
            border-radius: 0;
        }
    }
    .list-wrap{
        .title{
            margin:10px 0;
        }
    }
}